.asistencia-confirma{
    input[type="checkbox"] {
        transform: scale(2); /* Escalar el tama単o del checkbox */
        margin-right: 10px;    /* A単adir margen a la derecha */
        cursor: pointer;
    }
}
#example{
    margin-bottom: 2rem;
}
.table-asistencia{
    background-color: $azulClaro;
    color: white;
    border-radius: 6px;
}
.dataTables_length{
    label{
        
        select{
            color: white;

            &:focus{
                background-color: $azulClaro;
                color: white;
            }
        }

    }
}
.dataTables_filter{
    label{
        input{
            color: white;
            background-color: $azulClaro;
            border: 1px solid white;
            border-radius: 6px;
            padding: 6px;
            margin-left: 10px;
            &:focus{
                background-color: $azulClaro;
                color: white;
            }
        }
    }
}
.dataTables_wrapper .dataTables_paginate .paginate_button.disabled, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:hover, .dataTables_wrapper .dataTables_paginate .paginate_button.disabled:active{
    background-color: white;
    border-radius: 10rem;
    margin-left: 1rem;
    margin-right: 1rem;
    cursor: pointer;
    color: white;
}
